import { createRouter, createWebHistory } from "vue-router";

const routes = [
  {
    path: "/",
    name: "HomeView",
    component: () => import("@/View/HomeView.vue"),
  },
  // {
  //   path: "/About",
  //   name: "AboutView",
  //   // component: () => import("@/View/HomeView.vue"),
  // },
  {
    path: "/Business",
    name: "BusinessView",
    component: () => import("@/View/HomeView.vue"),
  },
  {
    path: "/Creator",
    name: "CreatorView",
    component: () => import("@/View/CreatorView.vue"),
  },
  {
    path: "/Contact",
    name: "Contact",
    component: () => import("@/View/ContactView.vue"),
  },

  {
    path: "/scrollsize",
    name: "scrollsize",
    component: () => import("@/components/Sub/ScrollSize"),
  },
  {
    path: "/scrollsizecopy",
    name: "scrollsize2",
    component: () => import("@/components/Sub/ScrollSizecopy"),
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
